import React, { useState } from 'react';
import { useCookies } from 'react-cookie';

const CookieConsent = () => {
  const [cookies, setCookie] = useCookies(["cookieConsent"]);


  return (
    <div style={{  
     background: "rgb(247, 247, 247)",
        boxShadow: "rgba(0, 0, 0, 0.4) 0px 2px 20px",
        boxSizing: "border-box",
        margin: "16px auto",
        position: "fixed",
        padding: "0px 16px",
        bottom: "16px"    ,width:"400px"   }}>
    <h2>Bienvenue sur le site </h2>

<h1 style={{fontSize:"18px"}}> Victis Formation</h1>
<h5 style={{fontSize:"15px",fontWeight:"500"}} >Nous utilisons plusieurs services de mesure d'audience et de comportement sur notre site afin d'améliorer celui-ci.
Ces services sont désactivés par défaut, mais vous pouvez les activer à tout moment à votre convenance.
 <a style={{fontSize:"15px",color:"blue"}} href="/mentionsLegales">Lire la politique de confidentialité</a></h5>
      <button onClick={()=>    setCookie("cookieConsent", true)} style={{color: "#231ab4",background:"rgb(209, 227, 240)", fontSize: "25px",padding:"15px", display:"flex",border:"none",   marginTop: "0px" }}>
        Accept
      </button>
    </div>
  );
};

export default CookieConsent;
